import _ from "lodash";
import * as yup from "yup";
import { REGEX_PHONE_NUMBER } from "../../../../../../utils/REGEX";

export const handleValidateCustomer = () => {
  return yup.object().shape({
    name: yup
      .string()
      .nullable()
      .required("Tên khách hàng không được để trống")
      .max(255, "Tên khách hàng không được vượt quá 255 kí tự"),
    phone: yup
      .string()
      .required("Số điện thoại không được để trống")
      .test(
        "invalid_phone_number",
        "số điện thoại không đúng định dạng",
        function (phone_number) {
          const { path, createError } = this as any;
          if (_.isNil(phone_number) || phone_number.trim().length < 10) {
            return createError({
              path,
              message: "Số điện thoại phải chứa ít nhất 10 kí tự",
            });
          }
          return REGEX_PHONE_NUMBER.test(phone_number);
        }
      ),
  });
};
